<template>
  <div class="results">
    <h1>{{ title }}</h1>
    <h2>{{ description }}</h2>
    <div
      class="option"
      v-for="option in options" :key="option.id">
      <h3>{{option.text}}</h3>
      <div class="progress-bar">
        <div class="filled" :style="{width: option.votesFraction + '%'}"></div>
      </div>
      <h4>{{option.votesFraction}}%</h4>
    </div>
    <button class="poll-button" v-on:click="$emit('next')">Next</button>
  </div>
</template>

<script>
export default {
  props: ['title', 'description', 'totalVotes', 'selectedOption', 'options'],
  methods: {
    calcFraction(option) {
      return (option.votes / this.totalVotes) * 100
    }
  }
};
</script>

