import Vue from 'vue'
import PollViewer from './PollViewerRootContainer'

export default {
  install(vue){

    const root = new Vue({
      data: { pollId: document.getElementById('my-poll').dataset.pollId },
      render: createElement => createElement(PollViewer)
    })
    
    root.$mount(document.getElementById('my-poll'))
    
    vue.prototype.$pollViewer = root
  }
}
