<template>
  <div class="poll-card" :class="currentStep.type">
    <div class="poll" v-if="!showResults">
      <h1>{{ currentStep.title }}</h1>
      <h2>{{ currentStep.description }}</h2>
      <div class="options">
        <poll-option
          v-for="option in currentStep.options"
          :key="option.id"
          :text="option.text"
          :image="option.image"
          v-on:voted="voted(option)"
        ></poll-option>
      </div>
    </div>
    <div v-if="showResults">
      <poll-results
        :title="results.title"
        :description="results.description"
        :totalVotes="results.totalVotes"
        :selectedOption="results.selectedOption"
        :options="results.options"
        v-on:next="nextQuestion()"
      ></poll-results>
    </div>
  </div>
</template>

<script>
import PollDataService from "../../services/PollDataService";
import PollOption from "./PollOption.vue";
import PollResults from "./PollResults.vue";

export default {
  props: ["pollId"],
  components: { PollOption, PollResults },
  data() {
    return {
      pollData: null,
      step: {},
      showResults: false,
      results: null,
    };
  },
  computed: {
    currentStep: {
      get() {
        return this.step || {};
      },
      set(stepId) {
        this.step = this.pollData.steps.find((step) => step.id === stepId);
      },
    },
  },
  methods: {
    getPollData() {
      PollDataService.getPoll(this.pollId).then((result) => {
        this.pollData = result;
        this.currentStep = this.pollData.steps.find(
          (step) => step.id === this.pollData.firstStepId
        ).id;
      });
    },
    voted(option) {
      if (this.currentStep.options.length > 1) {
        this.toggleResults(option.id);
      }
      PollDataService.vote({ optionId: option.id }).then(() => {
        this.currentStep = option.nextStepId;
      });
    },
    toggleResults(optionId) {
      this.currentStep.totalVotes = this.currentStep.totalVotes + 1;
      this.results = {
        totalVotes: this.currentStep.totalVotes,
        selectedOption: optionId,
        title: this.currentStep.title,
        description: this.currentStep.description,
        options: this.currentStep.options.reduce((finalList, option) => {
          if (option.id === optionId) {
            option.votes = option.votes + 1;
          }
          finalList.push({
            id: option.id,
            text: option.text,
            votes: option.votes,
            votesFraction: Math.round((option.votes / this.currentStep.totalVotes) * 100),
          });
          return finalList;
        }, []),
      };
      this.showResults = true;
    },
    nextQuestion() {
      this.showResults = false;
    }
  },
  mounted() {
    this.getPollData();
  },
  beforeDestroy() {},
};
</script>

<style scoped>
h1 {
  font-weight: bold;
  font-size: 24px;
}
h2 {
  font-size: 16px;
}
.poll-card {
  text-align: center;
  background-color: #f5f5f5;
  padding-top: 20px;
  padding-bottom: 40px;
  min-height: 300px;
}
.poll-card.final {
  display: flex;
  justify-content: center;
  align-items: center;
}
.poll {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.poll.final {
  justify-content: center;
}
.poll.final .options {
  margin: unset;
}
.options {
  display: flex;
  justify-content: space-around;
  margin: auto 0;
}
</style>